.nav-container {
  display: flex;
  position: fixed;
  min-width: 100%;
  max-width: 100%;
  background: transparent;
  -webkit-transition: background .5s ease-in-out;
  transition: background .5s ease-in-out;
  z-index: 999;
}
.nav-inner-container {
  width: 95%;
  margin-left: auto;
  margin-right: auto;
  padding: 0.5rem 0;
  -webkit-transition: padding .5s ease-in-out;
  transition: padding .5s ease-in-out;
}

.nav-logo {
  float: left;
}
.nav-links {
  float: right;
}

.nav-items, .logo-container {
  display: inline-block;
  padding: 1rem;
  font-size: 1.25rem;
  color: white;
  cursor: pointer;
  -webkit-user-select: none;
}
.logo-container {
  font-size: 1.4rem;
  letter-spacing: 0.2rem;
  font-family: "Lato",sans-serif;
  padding-right: 0;
  padding-bottom: 0.8rem;
}
.logo-img {
  height: 1.4rem;
  width: 1.4rem;
  margin-right: 0.5rem;
}
.highlight {
  background: rgba(255,255,255,0.3);
}

.nav-items:hover {
  color: #ccc;
}

.nav-collapse {
  background: black;
}
.nav-collapse .nav-inner-container {
  padding: 0;
}

.hamburger-box {
  visibility: hidden;
}

/* mobile nav and small screens*/
@media only screen and (max-width: 700px) {
  .nav-container {
    background: black;
  }
  .nav-inner-container {
    width: 100%;
    padding: 0.2rem 0;
  }
  .nav-links {
    float: none;
    max-height: 0px;
    min-width: 100%;
    padding: 0;
    background: black;
    overflow: hidden;
    -webkit-transition: max-height 0.4s ease-in-out;
    transition: max-height 0.4s ease-in-out;
  }
  .nav-items {
    padding: 1rem 3rem;
    margin: auto;
    display: block;
    text-align: center;
  }
  .menu-open + .nav-links {
    -webkit-transition: max-height 0.4s ease-in-out;
    transition: max-height 0.4s ease-in-out;
    max-height: 600%;
  }

  .logo {
    margin-left: 1rem;
  }
  .hamburger-box {
    margin-right: 1rem;
    float: right;
    width: 1.7rem;
    border-radius: 100vh;
    cursor: pointer;
    padding: 1.5rem 1rem;
    visibility: visible;
  }
  .hamburger {
    position: relative;
    display: block;
  }
  .hamburger, .hamburger::before, .hamburger::after {
    width: 1.65rem;
    height: 0.1875rem;
    background: white;
    -webkit-transition-property: -webkit-transform opacity background-color;
    transition-property: -webkit-transform opacity background-color;
    transition-property: transform opacity background-color;
    transition-property: transform opacity background-color, -webkit-transform opacity background-color;
    -webkit-transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
            transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
    -webkit-transition-duration: 1s;
            transition-duration: 1s;
  }
  .hamburger::before, .hamburger::after {
    position: absolute;
    content: '';
    left: 0;
  }
  .hamburger::before {
    bottom: 0.5rem;
  }
  .hamburger::after {
    top: 0.5rem;
  }

  /* Hamburger hover animation*/
  .hamburger-box:hover .hamburger {
    -webkit-animation: slide 1.5s infinite 0.2s;
            animation: slide 1.5s infinite 0.2s;
  }
  .hamburger-box:hover .hamburger::after {
    -webkit-animation: slide 1s infinite 0.5s;
            animation: slide 1s infinite 0.5s;
  }
  .hamburger-box:hover .hamburger::before {
    -webkit-animation: slide 2s infinite;
            animation: slide 2s infinite;
  }
  @-webkit-keyframes slide {
    0% {
      width: 1.65rem;
    }
    50% {
      width: 0.7rem;
    }
    100% {
      width: 1.65rem;
    }
  }
  @keyframes slide {
    0% {
      width: 1.65rem;
    }
    50% {
      width: 0.7rem;
    }
    100% {
      width: 1.65rem;
    }
  }

  /* Open Menu and change Hamburger to an X*/
  .menu-open .hamburger {
    background-color: transparent;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    -webkit-animation: none;
            animation: none;
  }
  .menu-open .hamburger::before {
    -webkit-transform: translateY(8px) rotate(-45deg);
            transform: translateY(8px) rotate(-45deg);
    -webkit-animation: none;
            animation: none;
  }
  .menu-open .hamburger::after {
    -webkit-transform: translateY(-8px) rotate(45deg);
            transform: translateY(-8px) rotate(45deg);
    -webkit-animation: none;
            animation: none;
  }
  .menu-open .hamburger-box:hover .hamburger {
    -webkit-animation: none;
            animation: none;
  }
  .menu-open .hamburger-box:hover .hamburger::before {
    -webkit-animation: none;
            animation: none;
  }
  .menu-open .hamburger-box:hover .hamburger::after {
    -webkit-animation: none;
            animation: none;
  }

}
.home-container {
  display:flex;
  min-height: 85vh;
  min-width: 100%;
  position: relative;
  z-index: 2;
}
.home-lazyload-low-res {
  z-index: -3;
  background: url(/portfolio/static/media/mountain-top-1080.df3723e6.jpg);
}
.home-lazyload-mid-res {
  z-index: -2;
  background: url(/portfolio/static/media/mountain-top-1920.297ab920.jpg);
}
.home-lazyload-high-res {
  z-index: -1;
  background: url(/portfolio/static/media/mountain-top-4k.257c8a86.jpg);
}
.home-lazyload-low-res, .home-lazyload-mid-res, .home-lazyload-high-res {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-blend-mode: multiply;
  min-height: 100%;
  min-width: 100%;
  position: absolute;
}

.hero-overlay {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.8)), color-stop(17%, rgba(0, 0, 0, 0.73)), color-stop(35%, rgba(0, 0, 0, 0.66)), color-stop(62%, rgba(0, 0, 0, 0.55)), to(rgba(0, 0, 0, 0.4)));
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0.73) 17%, rgba(0, 0, 0, 0.66) 35%, rgba(0, 0, 0, 0.55) 62%, rgba(0, 0, 0, 0.4) 100%);
  background-attachment: fixed;
  min-height: 85vh;
  min-width: inherit;
}
.home-text {
  min-height: inherit;
}
.home-title {
  color: white;
  font-family: 'Lato', sans-serif;
  letter-spacing: 3px;
  font-weight: 300;
  text-align: center;
  font-size: 3.8rem;
  margin-top: 0;
  margin-bottom: 1.8rem;
  padding-top: 29vh;
  -webkit-animation: fadeInDown 1s;
          animation: fadeInDown 1s;
}

.home-description-container {
  display: flex;
  justify-content: center;
  -webkit-animation: fadeInUp 1s;
          animation: fadeInUp 1s;
}

.home-normal-text, .home-typing-text {
  color: white;
  text-align: center;
  font-family: "Josefin Sans", sans-serif;
  font-size: 1.2rem;
  -webkit-animation: fadeInUp 1s;
          animation: fadeInUp 1s;
}
.home-blinking-cursor {
  margin-left: 0.1rem;
  -webkit-animation: blinking 1s infinite;
          animation: blinking 1s infinite;
}

.home-arrow-container {
  align-items: center;
  margin: auto;
  height: 4rem;
  width: 4rem;
  margin-top: 13rem;
}
.home-arrow-container:hover {
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}
.home-arrow-container:hover .expand-more-icon {
  fill: #fff;
}
/*portrait screens*/
@media only screen and (max-height: 850px) {
  .home-arrow-container {
    margin-top: 9rem;
  }
}
/*landscape screens*/
@media only screen and (max-height: 420px) {
  .home-arrow-container {
    margin-top: 2rem;
  }
}

.expand-more-icon {
  height: 4rem;
  width: 4rem;
  fill: #ccc;
  cursor: pointer;
  -webkit-animation: fadeInDown 1s forwards;
          animation: fadeInDown 1s forwards;
  opacity: 0;
  -webkit-animation-delay: 0.8s;
          animation-delay: 0.8s;
}

@-webkit-keyframes fadeInDown{
  0% {
    opacity: 0;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%)
  }
  60% {
    opacity: 0.3;
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}

@keyframes fadeInDown{
  0% {
    opacity: 0;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%)
  }
  60% {
    opacity: 0.3;
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}

@-webkit-keyframes fadeInUp{
  0% {
    opacity: 0;
    -webkit-transform: translateY(400%);
            transform: translateY(400%)
  }
  60% {
    opacity: 0.3;
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}

@keyframes fadeInUp{
  0% {
    opacity: 0;
    -webkit-transform: translateY(400%);
            transform: translateY(400%)
  }
  60% {
    opacity: 0.3;
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
  }
}

@-webkit-keyframes blinking {
  0%{color: #fff;}
  49%{color: #fff;}
  60%{color: transparent;}
  99%{color:transparent;}
  100%{color: #fff;}
}

@keyframes blinking {
  0%{color: #fff;}
  49%{color: #fff;}
  60%{color: transparent;}
  99%{color:transparent;}
  100%{color: #fff;}
}
.project-card-container {
  z-index: 10;
  border-radius: 1vh;
  box-sizing: border-box;
  background: lightgrey;
  overflow: hidden;
  width: 100%;
  max-width: 20rem;
  height: 13.5rem;
  position: relative;
  margin: 1rem;
}
@media only screen and (min-width: 1200px) {
  .project-card-container {
    max-width: 25rem;
    height: 16.825rem;
  }
}
@media only screen and (max-width: 480px) {
  .project-card-container {
    margin: 0.4rem;
    margin-bottom: 1rem;
  }
}

.project-card-image {
  width: inherit;
  height: inherit;
  z-index: -10;
  position: absolute;
}
.project-card-overlay {
  height: inherit;
  width: inherit;
  position: absolute;
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}
.project-card-overlay:hover {
  background: rgba(1,1,1,0.6);
}
.project-card-title {
  color: transparent;
  -webkit-user-select: none;
  text-align: center;
  position: absolute;
  padding: 0.3rem;
  top: -2rem;
  left: 0;
  right: 0;
  color: white;
  font-family: 'Lato', sans-serif;
  letter-spacing: 2px;
  font-weight: 400;
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
  opacity: 0;
}
.project-card-overlay:hover .project-card-title {
  top: 0;
  left: 0;
  right: 0;
  opacity: 1;
}
.project-card-actions {
  display: grid;
  grid-template-columns: 50% 50%;
  height: 2.5rem;
  width: inherit;
  position: absolute;
  bottom: -2rem;
  left: 0;
  right: 0;
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
  opacity: 0;
}
.project-card-overlay:hover .project-card-actions {
  bottom: 1rem;
  left: 0;
  right: 0;
  opacity: 1;
}
.project-card-button {
  color: transparent;
  width: 50%;
  background: transparent;
  color: white;
  font-size: 1.2rem;
  text-decoration: none;
  justify-self: center;
  text-align: center;
  padding: 0.6rem;
  cursor: pointer;
  border: solid;
  border-color: white;
  border-radius: 0.4rem;
  border-width: 0.1rem;
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}
.project-card-button:hover {
  width: 60%;
  background: rgba(255,255,255,0.1);
}



